md-input-container {
  display: inline-block;
  position: relative;
  font-family: Roboto, "Helvetica Neue", sans-serif;
  line-height: normal;
  text-align: left; }
  [dir='rtl'] md-input-container {
    text-align: right; }

.md-input-element::placeholder {
  color: transparent; }

.md-input-element::-moz-placeholder {
  color: transparent; }

.md-input-element::-webkit-input-placeholder {
  color: transparent; }

.md-input-element:-ms-input-placeholder {
  color: transparent; }

.md-end .md-input-element {
  text-align: right; }
  [dir='rtl'] .md-end .md-input-element {
    text-align: left; }

.md-input-prefix, .md-input-suffix {
  width: 0.1px;
  white-space: nowrap; }
